import React from 'react';
import styled from 'styled-components';
import { colors } from '@components/typo';
import { FaRegStar, FaStar, FaStarHalf } from 'react-icons/fa';
import ReactStars from 'react-rating-stars-component';
import {Input} from '@components/libs/Forms'

const Container = styled.div`
    box-shadow: 3px 12px 24px 13px rgba(0, 0, 0, 0.03);
    width: 100%;
    padding: 10px;
    background: #fff;
    margin-top: 10px;
    border-radius: 10px;
`;

const Header = styled.div`
    display: flex;
    align-items: center;
    border-bottom: 1px solid ${colors.grey};
    padding-bottom: 10px;
`;
const AvatarContainer = styled.div`
    height: 50px;
    width: 50px;
    border-radius: 30px;
    background: ${colors.grey};
`;
const UserContent = styled.div`
    flex: 1;
    margin-left: 15px;
`;
const Username = styled.div`
    font-weight: 700;
`;
const UserLocation = styled.div`
    color: ${colors.textColor2};
    font-size: 12px;
`;

const ContentTitle = styled.div`
    font-size: 16px;
    font-weight: 600;
    margin-top: 5px;
    margin-bottom: 5px;
`;
const Contents = styled.div``;

const Footer = styled.div`
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
`

const FormField = styled(Input)`
    flex: 1;
    height: 40px;
`
const Button = styled.div`
    height: 40px;
    width: 40px;
    background-color: ${colors.defaultColor};
    display: flex;
    justify-content: center;
    align-items: center;
`

export default function ReviewFeed({data}) {
    return (
        <Container>
            <Header>
                <AvatarContainer></AvatarContainer>
                <UserContent>
                    <Username>Olufemi Alabi</Username>
                    <UserLocation>NG</UserLocation>
                </UserContent>
            </Header>
            <Contents>
                <ContentTitle>Comment Title</ContentTitle>
                <ReactStars
                    edit={false}
                    value={parseInt(4)}
                    count={5}
                    size={24}
                    isHalf={true}
                    emptyIcon={<FaRegStar />}
                    halfIcon={<FaStarHalf />}
                    fullIcon={<FaStar />}
                    activeColor="#ff6600"
                />
               Comments Review
            </Contents>
            <Footer>
                <FormField />
                <Button />
            </Footer>
        </Container>
    );
}
