import React, { useState } from 'react';
import AsyncCreatableSelect from 'react-select/async-creatable';
import SojiBaseInit, { config } from '@modules/SojiBase';
import { colors } from '@components/typo';
import Cookies from 'js-cookie';
import axios from 'axios';
import './index.css';

function AutoSuggest(props) {
    const [inputValue, setValue] = useState('');

    // handle input change event
    const handleInputChange = (value) => {
        setValue(value);
    };

    // load options using API call
    const loadOptions = async (inputValue) => {
        const payload = { search: inputValue };
        const apiURL = config.apiBaseUrl + 'v1/utils/companies/autosuggest';
        const request = await axios({
            method: 'post',
            url: apiURL,
            data: payload,
            headers: {
                'content-type': 'application/json',
                'access-control-allow-origin': '*',
                'authorization': config.systemKey + ':' + Cookies.get('SOJI_uft_token'),
            },
        });
        const getResponse = await request.data;
        if (getResponse.response) {
            if (getResponse.response.data.length > 0) {
                return getResponse.response.data;
            } else {
                return [
                    {
                        companyName: inputValue,
                        pageHandle: inputValue.toLowerCase().replace(/\W/g, ''),
                    },
                ];
            }
        } else {
            return [];
        }
    };

    const customStyles = {
        menuPortal: (base) => ({ ...base, zIndex: 9999 }),
        menu: (provided) => ({ ...provided, zIndex: '9999 !important' }),
        option: (provided, state) => {
            return {
                ...provided,
                'borderBottom': '1px solid #f8f8f8',
                'color': 'black',
                'backgroundColor': 'white',
                '&:hover': {
                    backgroundColor: '#f8f8f8',
                    color: 'black',
                },
                'height': '50px',
                'menuPortal': (provided) => ({ ...provided, zIndex: 9999 }),
                'menu': (provided) => ({ ...provided, zIndex: 9999 }),
            };
        },
        control: (styles) => ({
            ...styles,
            '&:hover': {
                border: '1px solid rgba(18, 18, 18, 0.3)',
            },
            'border': '1px solid rgba(18, 18, 18, 0.1)',
            'boxShadow': 'none',
            'borderRadius': '10px',
        }),
        singleValue: (provided, state) => {
            const opacity = state.isDisabled ? 0.5 : 1;
            const transition = 'opacity 300ms';
            return { ...provided, opacity, transition };
        },
        placeholder: (provided) => {
            return { ...provided };
        },
        indicatorSeparator: (provided) => {
            return {
                indicatorSeparator: null,
            };
        },
        dropdownIndicator: (provided) => {
            return {
                ...provided,
                'color': '#0B973A',
                '&:hover': {
                    color: 'rgba(20, 97, 101, 255)',
                },
            };
        },
        input: (provided) => {
            return {
                ...provided,
            };
        },
    };

    return (
        <AsyncCreatableSelect
            cacheOptions
            defaultOptions
            getOptionLabel={(e) => e.companyName}
            getOptionValue={(e) => e.pageHandle}
            loadOptions={loadOptions}
            onInputChange={handleInputChange}
            style={{ height: '50px', width: '100%' }}
            styles={customStyles}
            {...props}
        />
    );
}

export default AutoSuggest;
