import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
    box-shadow: 1px 1px 4px 0px rgb(0 0 0 / 25%);
    width: 100%;
    padding: 10px;
    background: #fff;
    margin-top: 10px;
    position: sticky;
    border-radius: 10px;
    top: 90px;
    margin-bottom: 10px;
`;

export default function ReviewStats({ data }) {
    return (
        <Container>
            <div className="ui">
                <div className="ui_box">
                    <div className="ui_box__inner">
                        <h2>Total Rating</h2>
                        <div className="stat">
                            <span>58%</span>
                        </div>
                        <div className="progress">
                            <div className="progress_bar"></div>
                        </div>
                    </div>
                </div>

                <div className="ui_box">
                    <div className="ui_box__inner">
                        <h2>Total Reviews</h2>
                        <div className="stat">
                            <span>58%</span>
                        </div>
                        <div className="progress">
                            <div className="progress_bar"></div>
                        </div>
                    </div>
                </div>
            </div>
        </Container>
    );
}
